<template>
  <div class="wrap" v-for="squre in squreData.filter((v)=> v.squre === this.focus)" :key="squre">
    <img :src="squre.imgSrc">
    <strong>{{ squre.title }}</strong>
    <pre>{{ squre.content }}</pre>
  </div>
</template>

<script>
import squreData from '@/assets/squreData';

export default {
  props : ["focus"],
  data () {
    return {
      squreData,
    }
  }
}
</script>

<style lang="scss" scoped>
    .wrap {border: 1px solid #ddd; width: 700px;
      img {max-width: 100%;}
      strong {display: block; font-weight: bold; margin: 30px 0;}
      pre {white-space: pre-wrap;}
    }
</style>