<script>
export default {
    props : ['focus'],
    data() {
        return {
            squreTarget : undefined,
            clientRect : undefined,
            relativeTop : undefined,
            relativeLeft : undefined,
            focus : this.focus
        }
    },
    methods : {
    },
    created() {
    },
    mounted() {
        const board = document.getElementById('board');
        this.squreTarget = document.querySelectorAll('.squre ul li');
        this.clientRect = document.getElementById('center').getBoundingClientRect(),
        this.relativeTop = this.clientRect.top,
        this.relativeLeft = this.clientRect.left
        this.squreTarget.forEach((item,index) => {
            let nowTop = this.relativeTop+50 - item.getBoundingClientRect().top
            let nowLeft = this.relativeLeft+50 - item.getBoundingClientRect().left
            item.addEventListener('click', () => {
                this.$parent.changeSqure(`squre${index}`)
                board.style.top = `${nowTop}px`
                board.style.left = `${nowLeft}px`
                this.squreTarget.forEach(v=>{
                    v.style.boxShadow = 'none'
                })
                if (item.classList.contains('focus')) {
                    board.style.transform = 'scale(1)'
                    item.classList.remove('focus')
                    board.style.top = `50px`
                    board.style.left = `50px`
                } else {
                    board.style.transform = 'scale(1.5)'
                    item.style.boxShadow = '0 0 15px 2px #ff7272'
                    this.squreTarget.forEach(v=>{
                        v.classList.remove('focus')
                    })
                    item.classList.add('focus')
                }
            })
        })
    }
}
</script>

